import styled, { keyframes } from "styled-components";

function StackSlider() {
  const imgs = [
    "python",
    "django",
    "flask",
    "javascript",
    "node",
    "express",
    "react",
    "redux",
    "formik",
    "postgresql",
    "mongodb",
    "selenium",
    "websocket",
    "bootstrap",
    "cypress",
  ];

  return (
    <StackSliderWrapper>
      {imgs.map((name, i) => (
        <Image order={i} alt={name.toUpperCase()} src={name + ".png"} />
      ))}
    </StackSliderWrapper>
  );
}

const StackSliderWrapper = styled.div`
  margin-top: 2.2rem;
  &:hover > img {
    animation-play-state: paused;
  }
`;

const fadeIn = keyframes`
  0% {
    opacity: 0;
  }
  33% {
    opacity: 1;
  }
  66% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
`;

const translate = keyframes`
  from {
    left: 0;
  }
  to {
    left: calc(100% - 70px);
  }
`;

const Image = styled.img`
  position: absolute;
  left: 0;
  height: 70px;
  opacity: 0;
  animation: 15s ${fadeIn} linear ${({ order }) => order * 1000}ms infinite
      running,
    15s ${translate} linear ${({ order }) => order * 1000}ms infinite running;
`;

export default StackSlider;
